import styled from "styled-components";

const BtContent = () => {
  return (
    <Container>
      <h2>EDUCATION</h2>
      <TitleWrap>
        <Title></Title>
        <div className="subTitle">
          <p></p>
          <p>2024  - </p>
        </div>
      </TitleWrap>
      <TitleWrap>
        <Title>-</Title>
        <div className="subTitle">
          <p></p>
          <p>2018  - 2022 </p>
        </div>
      </TitleWrap>
    </Container>
  );
};

export default BtContent;

const Container = styled.div`
  width: 855px;
  margin: 0 auto;
  margin-top: 24px;
  padding-bottom: 30px;
  h2 {
    font-size: 32px;
    color: #fffdfd;
  }
`;
const TitleWrap = styled.div`
  width: 314px;
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  .subTitle {
    width: 155px;
    font-size: 14px;
    font-weight: 500;
    color: #fff;
  }
`;
const Title = styled.p`
  font-weight: bold;
  color: #f8f4d7;
`;
