import styled from "styled-components";
import Nav from "../components/Nav";
import mailIcon from "../assets/mail.svg";
import githubIcon from "../assets/github.svg";
const ContactPage = () => {
  return (
    <Container>
      <Nav />
      <ContactWrap>
        <h1>CONTACT</h1>
        <ItemBox>
          <TextWrap>
            <p>
                     ,
              <br />
              BENJAMIN FRANKLIN  '     
              <br />
               '       . 
              <br />
                  ,    
              <br />
                 .   , 
              <br />     .
            </p>
          </TextWrap>
          <IconWrap>
            <div>
              <p></p>
              <a href="mailto:parkjin0115@naver.com">
                <img src={mailIcon} alt=" " />
              </a>
            </div>
            <div>
              <p>github</p>
              <a href="https://github.com/jin-park0115">
                <img src={githubIcon} alt=" " />
              </a>
            </div>
          </IconWrap>
        </ItemBox>
      </ContactWrap>
    </Container>
  );
};

export default ContactPage;

const Container = styled.div`
  width: 1045px;
  display: flex;
  margin: 0 auto;
  margin-top: 72px;
`;

const ContactWrap = styled.div`
  width: 855px;
  display: flex;
  flex-direction: column;
  gap: 100px;
  h1 {
    font-size: 32px;
    color: #f8f4d7;
  }
`;
const ItemBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 100px;
`;
const TextWrap = styled.div`
  color: #f8f4d7;
  background-color: #bd42e9;
  border-radius: 10px;
  padding: 20px 10px;
`;
const IconWrap = styled.div`
  display: flex;
  gap: 20px;
  div {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  p {
    color: #f8f4d7;
  }
  img {
    height: 50px;
  }
`;
